import uniStatusBar from "../../components/uni-status-bar/uni-status-bar.vue"
import otherLogin from "../../components/profile/other-login.vue"
export default {
	components:{
		uniStatusBar,
		otherLogin
	},
	data() {
		return {
			status:false, /* 账号密码登录 */
			loading:false,
			disable:true,
			
			username:"",
			password:"",
			phone:"",
			checknum:"",
			codetime:0
		};
	},
	watch:{
		username(val){
			this.onBtnChange()
		},
		password(val){
			this.onBtnChange()
		},
		phone(val){
			this.onBtnChange()
		},
		checknum(val){
			this.onBtnChange()
		}
	},
	methods:{
		/* 验证手机号合法性 */
		checkPhone(){
			var mPattern = /^1[34578]\d{9}$/
			return mPattern.test(this.phone)
		},
	 	async getCheckNum(){
			if(!this.checkPhone()){
				uni.showToast({
					title: '手机号错误'
				});
				return
			}
			if(this.codetime > 0){
				uni.showToast({
					title: '请不要重复获取',
					icon:"none"
				});
				return 
			}
			/* 获取手机验证码 */
			let [err, res] = await this.$http.post("/user/sendcode", {
				phone:this.phone
			},{ token:false })
			console.log(res);
			this.util.CheckError(err, res)
			if(res.data.errorCode == 30001 ) return

			/* 重复获取时 的延迟 */
			this.codetime = 60
			let time = setInterval(()=>{
				this.codetime--
				if(this.codetime < 1){
					clearInterval(time)
					this.codetime = 0
				}
			},1000)
		},
		initInput(){
			this.phone = ''
			this.password = ''
			this.checknum= ''
			this.username= ''
		},
		onBtnChange(){
			if((this.username && this.password)||(this.phone && this.checknum)){
				this.disable = false
				return
			}
			this.disable = true
		},
		changeStatus(){
			this.initInput()
			this.status = !this.status
		},
		submmit(){
			/* 账号密码登录 */
			if(this.status){
				this.User.login({
					url:"/user/login",
					data:{
						username:this.username,
						password:this.password
					}
				})
				return
			}
			
			/* 验证码登录 验证手机号 */
			if(!this.checkPhone()){
				uni.showToast({
					title: '手机号错误'
				});
				return
			}
			this.User.login({
				url:"/user/phonelogin",
				data:{
					phone:this.phone,
					code:this.checknum
				}
			})
		},
		navBack(){
			uni.navigateBack({
				delta: 1
			});
		}
	}
}
